import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import { useParams } from "react-router-dom";
import { InlineMath } from 'react-katex';
import 'katex/dist/katex.min.css';
import { PulseLoader } from 'react-spinners';  // Import the spinner
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPaperPlane, faPaperclip, faTimesCircle } from '@fortawesome/free-solid-svg-icons';
import './App.css';

// Helper function to parse LaTeX math expressions from text
const parseMath = (text) => {
  const parts = text.split(/(\$.*?\$|\\\(.+?\\\)|\\\[.+?\\\])/);
  return parts.map((part, index) => {
    if (part.startsWith("$") || part.startsWith("\\(") || part.startsWith("\\[")) {
      const cleaned = part.replace(/^\$|\\\(|\\\[|\$|\\\)|\\\]/g, "");
      return <InlineMath key={index}>{cleaned}</InlineMath>;
    }
    return <span key={index}>{part}</span>;
  });
};

// Three-dot loading spinner component
const TypingIndicator = () => {
  return (
    <div className="typing-indicator">
      <span className="dot"></span>
      <span className="dot"></span>
      <span className="dot"></span>
    </div>
  );
};

const TaskPage = () => {
  const { taskId } = useParams(); // Extract taskId from the URL
  console.log("using task id from URL:", taskId); // Debugging
  const [claim, setClaim] = useState("");
  const [proofTask, setProofTask] = useState("");
  const [reflectionTasks, setReflectionTasks] = useState([]);
  const [conversation, setConversation] = useState([]);
  const [userInput, setUserInput] = useState("");
  const [sessionId, setSessionId] = useState("");
  const [stage, setStage] = useState("proof");
  const [reflectionResponses, setReflectionResponses] = useState([]);
  const [isWaitingForResponse, setIsWaitingForResponse] = useState(false); // Spinner control
  const [image, setImage] = useState(null);
  const [imageName, setImageName] = useState(""); // To hold the filename of the attached image


  const chatEndRef = useRef(null); // To scroll to bottom

  
  useEffect(() => {
    const fetchConfigurableData = async () => {
      try {
        console.log("Fetching task with ID:", taskId); // Debugging
        const response = await axios.post("https://hckrb8cp5e.execute-api.us-east-1.amazonaws.com/dev/start", { task_id: taskId });
        console.log("Backend response:", response.data); // Debugging
        setSessionId(response.data.session_id);
        setClaim(response.data.claim);
        setProofTask(response.data.student_proof_task);
        setReflectionTasks(response.data.student_reflection_tasks);
        setConversation([{ role: "chatbot", content: response.data.student_proof_task }]);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };
    
    if (taskId) {
      fetchConfigurableData();
    }
  }, [taskId]); // Ensure useEffect is triggered whenever taskId changes

  // Function to handle image selection
  const handleImageChange = (e) => {
    if (e.target.files.length > 0) {
      const file = e.target.files[0];
      setImage(file);
      setImageName(file.name);
    }
  };

  // Function to trigger file input dialog
  const handleFileClick = () => {
    document.getElementById('file-input').click();
  };


  // Function to clear the attached image
  const handleClearImage = () => {
    setImage(null);
    setImageName("");
  };

  // Scroll to bottom when a new message is added
  const scrollToBottom = () => {
    if (chatEndRef.current) {
      chatEndRef.current.scrollIntoView({ behavior: "smooth" });
    }
  };



  useEffect(() => {
    scrollToBottom(); // Scroll to the bottom when conversation updates
  }, [conversation]);

  const handleUserInput = async () => {
    if (stage === "proof") {
      // Store proof text and image for later analysis
      setConversation(prev => [...prev, { role: "student", content: userInput, image }]);
      setStage("reflection");
      setConversation(prev => [...prev, { role: "chatbot", content: reflectionTasks[0] }]);
    } else if (stage === "reflection") {
      const updatedReflections = [...reflectionResponses, userInput];
      setReflectionResponses(updatedReflections);
  
      // Immediately add the user's reflection to the conversation
      setConversation(prev => [...prev, { role: "student", content: userInput }]);
  
      if (updatedReflections.length === reflectionTasks.length) {
        // When all reflections are complete, proceed to analysis
        setStage("analysis");
        await analyzeWork(updatedReflections);
      } else {
        setConversation(prev => [...prev, { role: "chatbot", content: reflectionTasks[updatedReflections.length] }]);
      }
    }
    setUserInput(""); // Clear the input field
    //setImage(null); // Clear the attached image
    setImageName(""); // Clear the image filename
  };

  const analyzeWork = async (reflections) => {
    const proofMessage = conversation.find(msg => msg.role === "student" && msg.content && !msg.image);
    const studentProof = proofMessage ? proofMessage.content : "";

    setIsWaitingForResponse(true); // Start spinner
  
    const formData = new FormData();
    formData.append("session_id", sessionId);
    formData.append("student_proof", studentProof);
    reflections.forEach((reflection, index) => {
      formData.append(`student_reflections[]`, reflection);
    });
    if (image) {
      formData.append("image", image);
    }
    // Debug: Print out all the formData key-value pairs to check correctness
    for (let pair of formData.entries()) {
      console.log(`${pair[0]}:`, pair[1]);
    }
  
    try {
      const response = await axios.post("https://hckrb8cp5e.execute-api.us-east-1.amazonaws.com/dev/analyze", formData,{
        headers: {
          "Content-Type": "multipart/form-data"
        }
      });
      setConversation(prev => [...prev, { role: "chatbot", content: response.data.chatbot_reply }]);
      setStage("chat");
    } catch (error) {
      console.error("Error analyzing work:", error);
    }
  
    setIsWaitingForResponse(false); // Stop spinner
  };

  const continueConversation = async () => {
    // Immediately add the user's message to the conversation
    setConversation(prev => [...prev, { role: "student", content: userInput }]);
    setUserInput(""); // Clear the input field
  
    setIsWaitingForResponse(true); // Start spinner
  
    try {
      const response = await axios.post("https://hckrb8cp5e.execute-api.us-east-1.amazonaws.com/dev/continue", {
        session_id: sessionId,
        user_input: userInput
      });
      // Add the bot's response to the conversation
      setConversation(prev => [
        ...prev,
        { role: "chatbot", content: response.data.chatbot_reply }
      ]);
    } catch (error) {
      console.error("Error continuing conversation:", error);
    }
  
    setIsWaitingForResponse(false); // Stop spinner
  };

  return (
    <div className="App">
      <h1>CLAIM Bot ({taskId})</h1>

      <div className="claim">
        <strong>Claim:</strong> {claim}
      </div>
 
      <div className="conversation">
        {conversation.map((message, index) => (
          <div key={index} className={`chat-bubble ${message.role === "student" ? "student-message" : "chatbot-message"}`}>
            {message.content && <p>{parseMath(message.content)}</p>}
            {message.image && (
              <a href={URL.createObjectURL(message.image)} target="_blank" rel="noopener noreferrer">
                <img src={URL.createObjectURL(message.image)} alt="Student Work" className="attached-image" />
              </a>
            )}
          </div>
        ))}
        {isWaitingForResponse && (
          <div className="chat-bubble chatbot-message">
            <PulseLoader size={8} />
          </div>
        )}
        <div ref={chatEndRef}></div>
      </div>

      <div className="input-container">
        {stage === "proof" && (
          <button onClick={handleFileClick} className="icon-button attach-button" data-tooltip="Attach an image of your work">
            <FontAwesomeIcon icon={faPaperclip} />
          </button>
        )}
        <textarea
          value={imageName ? `Attached: ${imageName}` : userInput} // Show the filename if an image is attached
          onChange={(e) => {
            if (!imageName) {
              setUserInput(e.target.value); // Only allow user input if no image is attached
            }
          }}
          placeholder="Enter your response here"
          readOnly={!!imageName} // Make read-only when an image is attached
        />
        <input
          type="file"
          id="file-input"
          accept="image/*"
          onChange={handleImageChange}
          style={{ display: 'none' }} // Hide the default file input
        />
        {imageName && (
          <button onClick={handleClearImage} className="icon-button clear-button" data-tooltip="Remove attached image">
            <FontAwesomeIcon icon={faTimesCircle} />
          </button>
        )}
        <button onClick={stage === "chat" ? continueConversation : handleUserInput} className="icon-button send-button" data-tooltip="Submit">
          <FontAwesomeIcon icon={faPaperPlane} />
        </button>
      </div>
    </div>
  );
}

export default TaskPage;